.PanelHeader-content--brand,
.PanelHeader-bg--brand,
.Button--lvl-primary {
    background: #5965B3 !important;
}

.title_custom_main {
    font-family: 'Permanent Marker', cursive;
    font-size: 80px;
    text-align: center;
    font-weight: 900;
    color: rgba(175, 47, 47, 0.15);
    margin-bottom: 20px;
}

.todo-wrapper {
    max-width: 600px;
    margin: 0 auto;
    position: relative;
    background-color: #fff;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
}

.todo-input-wrapper {
    position: relative;
    border-bottom: 1px solid #ededed;
}

.todo-input {
    font-family: 'Indie Flower', cursive;
    padding: 16px 16px 16px 60px;
    border: none;
    background: rgba(0, 0, 0, 0.003);
    width: 100%;
    box-sizing: border-box;
    font-size: 28px;
    font-style: italic;
    box-shadow: inset 0 -2px 40px rgba(0, 0, 0, 0.03);
}

.fa-plus {
    position: absolute;
    font-size: 24px;
    top: 50%;
    transform: translateY(-50%);
    left: 20px;
}

.todo-input::-webkit-input-placeholder {
    font-family: 'Indie Flower', cursive;
}

.todo-input::-moz-placeholder {
    font-family: 'Indie Flower', cursive;
}

.todo-input:-moz-placeholder {
    font-family: 'Indie Flower', cursive;
}

.todo-input:-ms-input-placeholder {
    font-family: 'Indie Flower', cursive;
}

.todo-item {
    font-size: 24px;
    border-bottom: 1px solid #ededed;
    min-height: 60px;
    display: flex;
    align-items: center;
    list-style: none;
    padding: 0 16px 0 60px;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.text {
    font-family: 'Indie Flower', cursive;
}

.completed {
    text-decoration: line-through;
}

.completed,
.fa-check-circle {
    color: #ccc;
}

.fa-times {
    transition: all .3s ease;
    color: rgba(175, 47, 47, 0.15);
}

.mark {
    position: absolute;
    left: 20px;
}

.todo-item:hover .fa-times {
    color: rgba(175, 47, 47, 0.9);
}

.footer {
    position: relative;
    min-height: 50px;
    display: flex;
    align-items: center;
    padding: 0 6px 0 21px;
    justify-content: space-between;
}

.amount {
    font-size: 13.3333px;
}

.btn-group {
    position: relative;
    z-index: 1;
}

.filter-btn {
    margin: 3px;
    padding: 3px 7px;
    border: 1px solid transparent;
    border-radius: 3px;
    background-color: transparent;
    cursor: pointer;
}

.filter-btn:hover,
.filter-btn.active {
    border-color: rgba(175, 47, 47, 0.2);
}

.footer:before {
    content: '';
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 50px;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2), 0 8px 0 -3px #f6f6f6, 0 9px 1px -3px rgba(0, 0, 0, 0.2), 0 16px 0 -6px #f6f6f6, 0 17px 2px -6px rgba(0, 0, 0, 0.2);
}

.r-calendar {
    background-color: #eee;
    -moz-box-shadow: 0 0 5px #ccc;
    -webkit-box-shadow: 0 0 5px #ccc;
    box-shadow: 0 0 5px #ccc;
    font: normal 15px Helvetica Neue, Helvetica, Arial, sans-serif;
    min-width: 280px;
}

.r-calendar * {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    -moz-transition: all 0.3s linear;
    -webkit-transition: all 0.3s linear;
    -o-transition: all 0.3s linear;
    transition: all 0.3s linear;
}

.r-calendar .r-row {
    display: flex;
    flex-flow: row nowrap;
    width: 100%;
    justify-content: center;
}

.r-calendar .r-cell {
    background-color: #ffffff;
    align-items: center;
    display: flex;
    flex-flow: row nowrap;
    flex-grow: 1;
    flex-basis: 0;
    justify-content: center;
    word-wrap: break-word;
    overflow-wrap: break-word;
}

.r-calendar .r-head {
    flex-grow: 1;
    flex-basis: 0;
}

.r-calendar .r-prev,
.r-calendar .r-next {
    background-color: #ffffff;
    cursor: pointer;
    outline: none;
}

.r-calendar .r-prev:before,
.r-calendar .r-next:before {
    position: relative;
    top: 0;
    content: "";
    display: inline-block;
    width: 0.6em;
    height: 0.6em;
    border-top: 0.2em solid #222222;
    transform: rotate(-45deg);
}

.r-calendar .r-prev:before {
    border-left: 0.2em solid #222222;
    transform: rotate(-45deg);
    margin-left: 0.3em;
}

.r-calendar .r-prev:hover:before {
    border-left-color: #ffffff;
    border-top-color: #ffffff;
}

.r-calendar .r-next:before {
    border-right: 0.2em solid #222222;
    transform: rotate(45deg);
    margin-right: 0.3em;
}

.r-calendar .r-next:hover:before {
    border-right-color: #ffffff;
    border-top-color: #ffffff;
}

.r-calendar .r-prev:hover,
.r-calendar .r-next:hover {
    background-color: #0066CC;
}

.r-calendar .r-title {
    flex-grow: 5;
    flex-basis: max-content;
}

.r-calendar .r-weekdays {
    flex-grow: 1;
    flex-basis: 0;
}

.r-calendar .r-weekdays .r-cell {
    font-weight: bold;
}

.r-calendar .r-dates {
    display: flex;
    flex-flow: column nowrap;
    flex-grow: 6;
    flex-basis: 0;
}

.r-calendar .r-dates.r-fix {
    flex-grow: 5;
}

.r-calendar .r-dates .r-row {
    flex-grow: 1;
}

.r-calendar .r-dates .r-cell {
    background-color: #ffffff;
    border: solid 1px #ffffff;
}

.r-calendar .r-cell.r-date {
    cursor: pointer;
    outline: none;
}

.r-calendar .r-cell.r-date.r-today {
    background-color: #ffffff;
    border: solid 1px #0066CC;
}

.r-calendar .r-cell.r-date:not(.r-past):hover {
    background-color: #0066CC;
    border: solid 1px #0066CC;
    color: #ffffff;
}

.r-calendar .r-cell.r-date.r-selected {
    background-color: #000000;
    border: solid 1px #000000;
    color: #fff;
}

.r-calendar .r-cell.r-date.r-past {
    color: #999999;
    cursor: not-allowed;
}

.r-calendar .r-weeknum {
    color: #999999;
    font-size: 0.8em;
}

.r-calendar {
    position: relative;
    width: 100%;
}

.r-calendar:after {
    content: "";
    display: block;
    padding-bottom: 100%;
}

.r-calendar .r-inner {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: column nowrap;
}

.bgPreview {
    background-image: url('../image/pre.png');
    background-size: 72px;
    background-position: 0 0;
}


.customButton__ {
    -webkit-appearance: none;
    appearance: none;
    background: 0 0;
    border: 0;
    outline: 0;
    box-shadow: none;
    display: block;
    color: currentColor;
    padding: 0;
    margin: 0;
    font-size: 17px;
    position: relative;
    border-radius: 50%;
    font-weight: 500;
    font-size: 15px;
    margin-right: 18px;
    margin-bottom: 4px;
}

.custoBg__ .PanelHeader-bg--brand,
.custoBg__ .PanelHeader-left-in--brand, 
.custoBg__ .PanelHeader-addon--brand, 
.custoBg__ .PanelHeader-right--brand,
.custoBg__ .PanelHeader-content--brand {
    background: #E5E5E5 !important;
    color: #000
}

.custoBg__ .PanelHeader--android .PanelHeader-bg {
    box-shadow: none !important;
}

.EpicCustom__ .FormField {
    width: 100%;
}

.EpicCustom__ .Tabbar--android.Tabbar--shadow {
    box-shadow: none;
}

.EpicCustom__ .FormField__border {
    border: none;
    border-radius: 0
}

.customText___ {
    color: #000 !important;
    font-size: 16px;
    line-height: 20px;
}

.customAvatar___ {
    display: flex;
    align-items: flex-end;
}

.customInput____ .FormField__border {
    background-color: transparent !important;
    border: none !important;
}

.customButtonCow {
    background-color: #4986CC !important;
}

.customButtonCowGray {
    background-color: rgba(0, 28, 61, 0.05) !important;
    color: #3F8AE0 !important;
}